.allow-select-overflow {
    overflow: inherit !important;
}

.container-period {
    text-align: center;    
}

.container-period .period_class {
    color: #a52a2a;
    padding-top: 0.375rem;
}

.container-period .period_subject {
    color: orange;
    padding-top: 0.375rem;
}

.container-period .period_selection {
    padding-top: 0.375rem;
}

.container-period .teacher_subs {
    padding-top: 0.375rem;
    color: #008000;
}
.container-period .teacher_subs .remove {
    margin-left: 0.1875rem;
}

.container-reason {
    text-align: center;    
}

.container-reason .reason {
    padding-top: 0.375rem;
    color: #a52a2a;
}
.container-reason .reason .remove{
    margin-left: 0.1875rem;
}

.container-reason .reason-select {
    padding-top: 0.375rem;
}

.remove_row {
    display: flex;
    align-items: center;
    justify-content: center;
}

/* very hacky. explore react-bootstrap-table2 later */
.react-bs-container-body > table > tbody > tr > td:nth-child(1),
.react-bs-container-header > table > thead {
position: sticky;
position: -webkit-sticky;
background-color: white;
opacity: 0.9;
left:0;
z-index:1;
}
.react-bs-container-body > table > tbody > tr > td,
.react-bs-container-header > table > thead {
    vertical-align: middle;
}

.clickable {
    cursor:pointer;
}