.loader-full {
  border: 1rem solid #f3f3f3;
  border-radius: 50%;
  border-top: 1rem solid #3498db;
  width: 7.5rem;
  height: 7.5rem;
  -webkit-animation: spin 2s linear infinite; /* Safari */
  animation: spin 2s linear infinite;
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 1;
}

.loader-small {
  border: 0.25rem solid #f3f3f3;
  border-radius: 50%;
  border-top: 0.25rem solid #3498db;
  width: 1.875rem;
  height: 1.875rem;
  -webkit-animation: spin 2s linear infinite; /* Safari */
  animation: spin 2s linear infinite;
}

  .loading-layer {
    opacity: 0.1;
  }


  select
  {
  width: 7.5rem;
  }
  
  .FixedHeightContainer
  {
  border: 0.0625rem solid #dddddd;
  height: 15.625rem;
  width: 18.75rem;
  }
  .Content
  {
  display: block;
  height: 11.25rem;
  overflow-y:auto;
  background:#fff;
  }
  .innerblock
  {
  padding-top: 1.875rem
  }

  .innerblock .title {
      text-align: center;
      padding-bottom: 0.625rem;
      font-size: 120%;
  }
  
  .outer
  {
  display: block;
  margin-left: 1.25rem;
  margin-right: 1.25rem;
  }
  
  .center-block
  {
  text-align: center;
  }
  
  .active
  {
    font-weight: 700
  }
  
  .navbar-default {
      background-color: #3c8dbcc7;
      background-image: none;
  }
  @-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); }
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }